import logo from './images/profile.png';
import './App.css';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';


function App() {
    return (
        <div className="App">

            <Router>

                <img src={logo} className="profile-pic" alt="logo"/>
                <Routes>
                    <Route path='/test' element={test()}/>
                </Routes>
            </Router>

        </div>
    );
}

function test() {
    return (
        <div className="App">
            <header className="App-header">
                <p>test</p>
            </header>
        </div>
    );
}

export default App;
